import {Objet} from '../Objet.js';
import Text from './Text.svelte';

export class Trefle extends Objet {
    name = "Trfle";

    constructor (System) {
        super(System);

        this.init([["Or", 10], ["Vgtal", 10]]);
        this.familles.base.push("Plante");

        this.text = Text;
    };

    useEffect = function () {
        this.owner.discover(4);
        this.move("Dfausse");
        this.pose();
    };
}