import { Creature } from '../Creature.js';

export class GuerrierGeant extends Creature {
    name = "Guerrier gant";

    constructor(System) {
        super(System);

        this.init([["Or", 100]]);
        this.familles.base.push("Gant");
        
        this.stat("Vie").base = 75;
        this.stat("Vie").current = 75;
        this.stat("Attaque").base = 75;
        this.stat("Dfense").base = 25;
    };
}